idth:300px}@media screen and (max-width: 768px){.current__item{justify-content:center}}@media screen and (max-width: 578px){.current__item{width:100%;margin-bottom:30px}}.current__icon-b{padding:5px;display:flex;justify-content:center;border-radius:20px;background-color:#3d3d57;width:69px;height:100%}.current__icon{max-width:50%}.current__text{padding:0 10px;border-radius:10px;background-color:rgba(0,0,0,0.37);text-transform:capitalize;display:flex;align-items:center;margin-left:15px;height:100%;font-size:20px}.current__input{font-family:'Josefin Sans', sans-serif;height:80px;width:100%;background-color:transparent;border:0;border-bottom:1px rgba(245,245,245,0.534) solid;font-size:30px;color:whitesmoke}.current__input::-webkit-input-placeholder{font-size:30px;color:rgba(255,255,255,0.616)}.current__input:-ms-input-placeholder{font-size:30px;color:rgba(255,255,255,0.616)}.current__input::-ms-input-placeholder{font-size:30px;color:rgba(255,255,255,0.616)}.current__input::placeholder{font-size:30px;color:rgba(255,255,255,0.616)}.current__input:focus{outline:none}.current__search{width:40%}@media screen and (max-width: 768px){.current__search{width:80%;order:0;padding-bottom:50px}}.current__dropdown{opacity:0.8;margin:10px}.current__address{text-align:right;margin-top:25px;font-family:'Josefin Sans', sans-serif;font-size:20px;color:white}@media screen and (max-width: 768px){.current__address{text-align:center}}


